import { AxiosInstance } from "axios";
import { MapRetailer, MapRetailerSingle } from "src/interfaces/mapApiTypes";

export interface RetailersService {
  getAllRetailers: () => Promise<{ retailers: MapRetailer[], globalRetailerIds: number[] }>;
  getRetailer: (slug: string) => Promise<MapRetailerSingle>;
}

const createRetailersService = (axiosInstance: AxiosInstance): RetailersService => {
  const getAllRetailers = (): Promise<{ retailers: MapRetailer[], globalRetailerIds: number[] }> => {
    return new Promise((resolve, reject) => {
      let items: any[] = [];

      const fetchData = (currentPage: number = 1) => {
        axiosInstance(`/v1/retailers?limit=100&page=${currentPage}`)
          .then((response: any) => {
            if (response?.data?.items) {
              items = [...items, ...response.data.items];
              if (response.data.count > items.length) {
                fetchData(currentPage + 1);
              } else {
                const retailers: MapRetailer[] = [];
                const globalRetailerIds: number[] = [];

                for (const item of items) {
                  const retailer: Partial<MapRetailer> = {
                    id: item.id,
                    location: item.location,
                    retailer_phone: item.retailer_phone,
                    map_obj_name: ''
                  }

                  if (item.global_retailer) {
                    retailer.global_retailer_id = item.global_retailer.id;
                    retailer.retail_name = item.global_retailer.name;
                    retailer.slug = item.global_retailer.slug;
                    retailer.retailer_description = item.global_retailer.description;
                    retailer.logo = item.global_retailer.media?.url || '';
                  } else {
                    retailer.global_retailer_id = item.global_retailer_id;
                    retailer.retail_name = item.name;
                    retailer.slug = item.slug;
                    retailer.retailer_description = item.description;
                    retailer.logo = item.media.url || '';
                  }

                  retailers.push(retailer as MapRetailer);
                  globalRetailerIds.push(item.global_retailer_id);
                }

                // Sort retailers by retail_name alphabetically
                retailers.sort((a, b) => {
                  const an = (a.retail_name || '').toLowerCase();
                  const bn = (b.retail_name || '').toLowerCase();
                  if (an < bn) return -1;
                  if (an > bn) return 1;
                  return 0;
                });

                resolve({ retailers, globalRetailerIds });
              }
            }
          })
          .catch((err) => {
            console.error('Error fetching retailers', err);
            resolve({ retailers: [], globalRetailerIds: [] });
          });
      };

      fetchData();
    });
  }

  const getRetailer = (slug: string): Promise<MapRetailerSingle> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/retailers/${slug}`)
        .then((response: any) => {
          if (response?.data?.id) {
            const resp = response?.data;
            const retailer: MapRetailerSingle = {
              id: resp.id,
              global_retailer_id: resp.global_retailer_id,
              retail_name: resp.name,
              slug: resp.slug,
              retailer_description: resp.description,
              location: resp.location,
              retailer_phone: resp.retailer_phone,
              logo: resp.media.url || '',
              map_obj_name: '',
              opening_hours: resp.opening_hours
            };

            resolve(retailer);
          }
        })
        .catch((err) => {
          console.error('Error fetching retailers', err);
          reject(err);
        });
    });
  }

  return {
    getAllRetailers,
    getRetailer
  }
}

export default createRetailersService;

