import { Form, FormGroup, Modal } from "react-bootstrap";
import { XCircle } from "react-feather";
import { useEffect, useMemo, useState } from "react";
import styles from './RetailerInfoModal.module.css';
import { MapObjData, MapRetailer } from "src/interfaces/mapApiTypes";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { FormatPhoneNumber } from "src/helpers/misc.helper";
import Select from "react-select";

type RetailerInfoModalProps = {
  show: boolean,
  onHide: () => void,
  amenity: string,
  selectedAmenityMapObjName: string,
  mapObjectName?: string,
  onClickMapObj: (id: string, amenityType?: string) => void,
  allSvgObjectIds: Set<string>,
}

const RetailerInfoModal = ({ show, onHide, amenity, selectedAmenityMapObjName, mapObjectName, onClickMapObj, allSvgObjectIds }: RetailerInfoModalProps) => {

  const { mapConfig, allIndexedMapObjects, allIndexedRetailers } = useMapBoxContext();

  const [retailerInfo, setRetailerInfo] = useState<MapRetailer>();
  const [availableRetailersOnMap, setAvailableRetailersOnMap] = useState<MapRetailer[]>([]);
  const [availableGlobalRetailerIdsOnMap, setAvailableGlobalRetailerIdsOnMap] = useState<number[]>([]);

  const [selectedRetailerId, setSelectedRetailerId] = useState<number>(-1);

  useEffect(() => {
    if (mapObjectName) {
      const mapObj = allIndexedMapObjects[mapObjectName];
      if (mapObj && mapObj.retailer_id) {
        const retailer = allIndexedRetailers[mapObj.retailer_id];
        if (retailer) {
          setRetailerInfo(retailer);
        }
      }
    } else {
      setRetailerInfo(undefined);
    }
  }, [allIndexedMapObjects, allIndexedRetailers, mapObjectName]);

  const onMapItClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    e.stopPropagation();
    let mapObj: MapObjData;
    if (mapConfig.KIOSK_ID) {
      const filteredMapObjects = Object.values(allIndexedMapObjects).filter(obj => obj.kiosk_id === mapConfig.KIOSK_ID && allSvgObjectIds.has(obj.map_obj_name))
      mapObj = filteredMapObjects[0] as MapObjData;
    } else {
      const filteredMapObjects = Object.values(allIndexedMapObjects).filter(obj => obj.retailer_id === selectedRetailerId && allSvgObjectIds.has(obj.map_obj_name))
      mapObj = filteredMapObjects[0] as MapObjData; // just pick the first one
    }

    if (!mapObj) {
      console.error('Map object not found');
      return;
    }

    if (amenity) {
      onClickMapObj(mapObj.map_obj_name, selectedAmenityMapObjName);
    } else {
      onClickMapObj(mapObj.map_obj_name);
    }
  }

  useEffect(() => {
    const retailerIds = new Set(
      Object.values(allIndexedMapObjects)
        .filter(mapObj => mapObj.retailer_id !== null)
        .filter((mapObj) => allSvgObjectIds.has(mapObj.map_obj_name))
        .map(mapObj => mapObj.retailer_id as number)
    );

    const filteredMapRetailers = Array.from(retailerIds)
      .map(retailerId => allIndexedRetailers[retailerId])
      .filter(Boolean) // This filters out undefined values
      .sort((a, b) => a.retail_name.localeCompare(b.retail_name));

    setAvailableGlobalRetailerIdsOnMap(filteredMapRetailers.map(retailer => retailer.global_retailer_id));

    setAvailableRetailersOnMap(filteredMapRetailers);
  }, [allIndexedMapObjects, allIndexedRetailers, allSvgObjectIds])

  type RetailerOption = { value: number; label: string };

  const retailerOptions: RetailerOption[] = useMemo(() => {
    const options = availableRetailersOnMap
      .filter((item) => !(retailerInfo && item.id === retailerInfo.id))
      .map((item) => {
        const hasMultiple = availableGlobalRetailerIdsOnMap.filter(
          (id) => id === item.global_retailer_id
        ).length > 1;
        const label = hasMultiple && item.location
          ? `${item.retail_name} (${item.location})`
          : item.retail_name;
        return { value: item.id, label } as RetailerOption;
      });
    return options;
  }, [availableRetailersOnMap, availableGlobalRetailerIdsOnMap, retailerInfo]);

  const selectedRetailerOption = useMemo(() => {
    return retailerOptions.find((opt) => opt.value === selectedRetailerId) || null;
  }, [retailerOptions, selectedRetailerId]);

  const onChangeRetailerId = (option: RetailerOption | null) => {
    setSelectedRetailerId(option?.value ?? -1);
  };

  if (!show) return null;

  return (
    <Modal
      show={show}
      dialogClassName={styles.retailerInfoModal}
      className="px-2"
      onHide={onHide}
      centered
      scrollable={true}
      unmountOnClose
      onExited={() => setSelectedRetailerId(-1)}
    >
      <div className={styles.retailerInfoCloseBtn}>
        <XCircle onClick={onHide} size={24} color="white" className={styles.closeBtn} />
      </div>

      <Modal.Body className={styles.modalBody}>
        <div className={styles.contentWrapper}>
          {retailerInfo ? (
            <div className={styles.content}>
              <div className={styles.retailerLogo}>
                <img className={styles.img} src={retailerInfo.logo} alt={retailerInfo.retail_name} />
              </div>
              <div className={styles.retailerInfoContent} >
                <div><b>Retailer:</b> {retailerInfo.retail_name}</div>
                {retailerInfo.location && <div><b>Location:</b> {retailerInfo.location}</div>}
                {retailerInfo.retailer_phone && <div><b>Phone:</b> <a href={`tel:${retailerInfo.retailer_phone}`}>{FormatPhoneNumber(retailerInfo.retailer_phone)}</a></div>}
                {retailerInfo.retailer_description && <div dangerouslySetInnerHTML={{ __html: retailerInfo.retailer_description }}></div>}
              </div>
            </div>
          ) : null}
        </div>

        <Form className={styles.getDirectionsFrom}>
          <Select
            name="retailer"
            className={styles.retailerDropdown}
            classNamePrefix="react-select"
            menuPortalTarget={typeof document !== 'undefined' ? document.body : undefined}
            aria-label="Choose a Retailer"
            options={retailerOptions}
            isLoading={false}
            noOptionsMessage={() => 'Retailer not found'}
            placeholder="Get direction from ..."
            value={selectedRetailerOption}
            onChange={onChangeRetailerId}
            styles={{
              menuPortal: (base) => ({ ...base, zIndex: 2000 }),
              menu: (base) => ({ ...base, zIndex: 2000 }),
            }}
            isClearable
          />

          <button type="button" disabled={selectedRetailerId === -1} onClick={onMapItClick} className={styles.btn}>Map IT</button>
        </Form>
      </Modal.Body>
    </Modal>
  )
}

export default RetailerInfoModal;
